.bookCover{
    width: 120px;
    height: 190px;
}

.mt6{
 margin-top: 85px
}

.mb6{
 margin-bottom: 85px
}

.text{
    white-space: normal!important;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
    font-size: 14px;
}

.text h4{
    color: #1c4865;
    margin: 3px 0;
    font-size: 16px;
    line-height: 17px;
    max-height: 34px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: 700;
}

.gray {
    color: #999;
}

.blue {
    color: #1c4865;
}

.bgBlue {
    background-color: #1c4865;
}

.btnGhostViolet {
    color: #1c4865;
    background: transparent;
    border-color: #1c4865;
}

.flXl {
    font-size: 25px;
    color: #1c4865;
  }

.navStatus{
    background-color: #ffffff;
    width: 100%;
    padding: 5px;
}

.btnLink{
    color: #444;
    text-decoration: none;
    padding: 10px;
}

